<template>
	<div class="home_box">
		<div class="home_left ">
			<el-col :span="12" style="width: 200px;">
				<div class="home_left_login animated zoomIn" @click="$router.push({name:'admin_home'})">
					<img src="../../../static/img/logo.png" alt="">
				</div>
				<el-menu :default-active='Active_Index' router class="el-menu-vertical-demo" background-color="#545c64" text-color="#fff"
				 active-text-color="#ffd04b">
					<el-menu-item index="/Createactivity">
						<i class="el-icon-s-tools"></i>
						<span slot="title">活动设置</span>
					</el-menu-item>
				</el-menu>
			</el-col>
		</div>
		<div class="home_right">
			<div class="home_right_title animated fadeInDown">
				<div>
					<span>欢迎来到企程无忧建站后台</span>
					<span>咨询电话：13717971636</span>
					<span>
						<el-button type="text" @click='qcj'>去抽奖</el-button>
					</span>
				</div>
				<div class="tx_img">
					<el-popover placement="bottom" width="auto" trigger="hover">
						<p class="txk" @click="Cancellation"><i class="el-icon-circle-close txkd_i"></i>注销账号</p>
						<div class="tx" slot="reference">
							用户 : {{name}}
						</div>
					</el-popover>
				</div>
			</div>
			<div class="home_right_cont">
				<router-view></router-view>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				name: ''
			}
		},
		methods: {
			Cancellation() {
				this.$axios.post(this.$url + '/choujiang/exit')
				  .then((res) => {
				    // console.log(res)
				    if (res.data.result) {
				      this.$message({
				        message: res.data.msg,
				        showClose: true,
				        type: 'success'
				      });
				      localStorage.clear()
				      this.$router.push({
				        name: 'login'
				      })
				    }
				  })
			},
			qcj() {
				let routeUrl = this.$router.resolve({
					name: "luck_draw"
				});
				window.open(routeUrl.href, '_blank');
			}
		},
		created() {
			let yhxx = JSON.parse(localStorage.getItem('Yhxx'))
			this.name = yhxx.username
		},
		computed: {
			Active_Index() {
				// console.log(this.$route)
				// return this.$route.path
				if (this.$route.path.indexOf('Createactivity') == 1) {
					return '/Createactivity'
				}
				if (this.$route.path.indexOf('Jurisdiction') == 1) {
					return '/Jurisdiction'
				} else {
					return this.$route.path
				}
			}
		},
	}
</script>

<style scoped>
	.home_box {
		width: 100%;
		height: auto;
		display: flex;
	}

	.home_left {
		width: 199px;
		overflow: hidden;
		height: 100vh;
		position: fixed;
		z-index: 999;
		background: #545c64;
	}

	.home_left_login {
		width: 176px;
		height: 62px;
		margin: 15px auto;
		line-height: 50px;
		text-align: center;
		font-size: 18px;
		font-weight: bold;
		color: white;
		/* margin: 15px 0; */
	}

	.home_left_login img {
		width: 100%;
		height: 100%;
	}

	.home_right {
		height: auto;
		/* background: darkgoldenrod; */
		position: absolute;
		left: 200px;
		right: 0;
		-webkit-transition: .3s;
		transition: .3s;
	}

	.home_right_cont {
		width: 96%;
		background: #fff;
		height: auto;
		margin: 0 auto;
		margin-top: 55px;
	}

	.home_right_title {
		height: auto;
		/* background: darkgoldenrod; */
		position: absolute;
		left: 0;
		right: 0;
		-webkit-transition: .3s;
		transition: .3s;
		display: flex;
		justify-content: space-between;
		padding: 0 0 0 2%;
		box-shadow: 0 0 5px #f5f5f5;
	}

	.home_right_title span {
		line-height: 50px;
		font-size: 14px;
		font-weight: bold;
		color: #545c64;
		margin-right: 20px;
	}

	.tx_img {
		width: 170px;
		height: 50px;
		line-height: 50px;
		text-align: center;
		/* background: red; */
		color: #545c64;
	}

	.tx {
		/* color: #ccc; */
	}

	.txk {
		line-height: 40px;
		transition: .5s;
		text-align: center;
		cursor: pointer;
	}

	.txk:hover {
		background: #f1f1f1;
	}

	.txkd_i {
		margin-right: 10px;
	}
</style>



